import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Card, CardBody, CardTitle, Col, CustomInput, Row } from 'reactstrap';
import { DATES, InputGroupInline, onlyNumber, WindowComponent, WindowComponentProps } from 'summer';
import { hasEmpresa } from '../../../../../dependencies/empresaDependency';
import { hasEmpresaMCG } from '../../../../../dependencies/empresaMCGDependency';
import { getDefaultReportBasicValues } from '../../../../../models/dtos/ger/reportBasicValues';
import { getDefaultReportViewOptions, ReportViewOptions } from '../../../../../models/dtos/ger/reportViewOptions';
import { Reducers } from '../../../../../store/ducks';
import { GlobalState } from '../../../../../store/ducks/global.duck';
import ResponsavelIS from '../../../../components/ger/responsavelIS/ResponsavelIS';
import CustomizacaoTermoIS from '../../../../components/mcg/customizacaoTermoIS/CustomizacaoTermoIS';
import ReportForm from '../../../../components/report/ReportForm';
import { CLASS_KEY, REPORT_EVENT, REPORT_TITLE, TermoAberturaEncerramentoRV } from './termoAberturaEncerramentoRV';

const DEFAULT_VALUES: TermoAberturaEncerramentoRV = {
  ...getDefaultReportBasicValues(REPORT_TITLE),
  folhaFinal: 0,
  folhaInicial: 0,
  gerarCabecalhoPadraoAdministradora: true,
  livro: '1',
  numero: 1,
  representante: null,
};

const DEFAULT_OPTIONS: ReportViewOptions = {
  ...getDefaultReportViewOptions(REPORT_EVENT, DATES.value),
  exibirConfiguracaoDadosEmpresa: false,
  exibirSegmentacaoFolha: true,
};

const TermoAberturaEncerramentoReportView: FC<WindowComponentProps & RouteComponentProps> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const { handleSubmit, ...formProps } = useForm<TermoAberturaEncerramentoRV>({ defaultValues: DEFAULT_VALUES, mode: 'onBlur' });

  const validateExtras = () => {
    const erros = [];

    if (formProps.getValues().numero === 0) {
      erros.push('O Campo número não pode ser zero');
    }
    return erros;
  };

  return (
    <WindowComponent
      title="Termo de Abertura e Encerramento"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[() => hasEmpresa(globalReducer.globalParameter), () => hasEmpresaMCG(globalReducer.globalParameter)]}
    >
      <CustomizacaoTermoIS
        formProps={{ ...formProps, validation: { required: true } }}
        label="Termo de Abertura e Encerramento"
        placeholder="Selecione o Termo de Abertura e Encerramento"
        searchViewProps={{ apenasAberturaEncerramento: true }}
        apenasAberturaEncerramento={true}
        id="termo"
        isPrependLabel={true}
        inputSearchProps={{ size: 'xl' }}
        tooltipProps={{ message: 'Termo de Abertura e Encerramento.' }}
      />
      <Card className="mb-3">
        <CardBody>
          <CardTitle>Informações do Livro</CardTitle>
          <Row>
            <Col>
              <InputGroupInline
                formProps={{ ...formProps, validation: { required: true, maxLength: 50 } }}
                id="livro"
                isPrependLabel={true}
                label="Livro"
                placeholder={'Informe o Livro'}
              />
            </Col>
            <Col>
              <InputGroupInline
                formProps={{ ...formProps, validation: { required: true, ...onlyNumber(0, 10) } }}
                id="numero"
                isPrependLabel={true}
                label="Número"
                placeholder={'Informe o Número'}
              />
            </Col>
          </Row>
          <Row>
            <Col>
              <InputGroupInline
                formProps={{ ...formProps, validation: { required: true, ...onlyNumber(0, 10) } }}
                id="folhaInicial"
                isPrependLabel={true}
                label="Folha Inicial"
                placeholder={'Informe a Folha Inicial'}
              />
            </Col>
            <Col>
              <InputGroupInline
                formProps={{ ...formProps, validation: { required: true, ...onlyNumber(0, 10) } }}
                id="folhaFinal"
                isPrependLabel={true}
                label="Folha Final"
                placeholder={'Informe a Folha Final'}
              />
            </Col>
          </Row>
        </CardBody>
      </Card>
      <Row className="mb-3">
        <Col>
          <CustomInput
            type="switch"
            label="Exibir Numeração da Página"
            name="exibirNroPaginaTermoAE"
            id="exibirNroPaginaTermoAE"
            innerRef={formProps.register}
          />
        </Col>
        <Col>
          <CustomInput type="switch" label="Ocultar Data do cabeçalho" name="ocultarData" id="ocultarData" innerRef={formProps.register} />
        </Col>
      </Row>
      <Row>
        <Col>
          <ResponsavelIS
            id="representante"
            formProps={formProps}
            label="Representante"
            placeholder="Informe o Representante"
            isPrependLabel={true}
            inputSearchProps={{ Size: 'xl', selectProps: { isMulti: false } }}
          />
        </Col>
      </Row>
      <ReportForm
        {...props}
        handleSubmit={handleSubmit}
        formProps={formProps}
        localStorageKey={CLASS_KEY}
        routeProps={props}
        reportBasicValues={DEFAULT_VALUES}
        reportViewOptions={DEFAULT_OPTIONS}
        validateExtras={validateExtras}
      />
    </WindowComponent>
  );
};

export default TermoAberturaEncerramentoReportView;
