import React, { FC } from 'react';
import { InputGroupInline, InputProps, InputType } from 'summer';
import { CLASS_NAME } from '../../../../models/dtos/ger/responsavel';

const ResponsavelIS: FC<InputProps> = props => {
  return (
    <InputGroupInline
      {...props}
      isPrependLabel={true}
      inputSearchProps={{
        ...props.inputSearchProps,
        columns: [
          {
            dataField: 'nome',
            text: 'Nome',
          },
          {
            dataField: 'cpf',
            text: 'CPF',
          },
          {
            dataField: 'cnpj',
            text: 'CNPJ',
          },
          {
            dataField: 'cei',
            text: 'CEI',
          },
          {
            dataField: 'tipo',
            text: 'Tipo',
          },
          {
            dataField: 'telefone',
            text: 'Telefone',
          },
          {
            dataField: 'email',
            text: 'E-mail',
          },
        ],
        dtoClassName: CLASS_NAME,
        searchViewId: 'ResponsavelSearchView',
      }}
      label={props.label ? props.label : 'Responsável'}
      placeholder={props.placeholder ? props.placeholder : 'Informe o Responsável'}
      type={InputType.INPUT_SEARCH}
    />
  );
};

export default ResponsavelIS;
