import { Basic } from 'summer';
import { CepBairro } from './cepBairro';
import { CepLocalidade } from './cepLocalidade';
import { CepTitulo } from './cepTitulo';
import { CepUF } from './cepUF';

export interface Responsavel extends Basic {
  cargo?: string;
  cei?: string;
  cep?: string;
  cnpj?: string;
  cpf?: string;
  cepBairro?: CepBairro;
  /** Campo referente a cidade */
  cepLocalidade?: CepLocalidade;
  /** tipo do logradouro ex: rua, avenida */
  cepTitulo?: CepTitulo;
  cepUf?: CepUF;
  complemento?: string;
  dataNascimento?: Date;
  dataValidadeCRC?: Date;
  email?: string;
  /** Este campo utiliza o estadoCivilEnum */
  estadoCivil?: string;
  logradouro?: string;
  numeroConselho?: string;
  /** Este campo utiliza o nacionalidadeEnum */
  nacionalidade?: string;
  nome?: string;
  numero?: string;
  orgaoEmissor?: string;
  rg?: string;
  telefone?: string;
  /** Este campo utiliza o responsavelTipoEnum */
  tipo?: string;
}

export const CLASS_NAME = 'br.com.projetusinformatica.calima.ger.model.dto.responsavel';
